.brand .slider .bx-wrapper .bx-controls-direction a {
  display: none;
}

.brand .slider .bx-wrapper .bx-viewport {
  overflow: initial;
  margin-left: 15px;
  height: 61px !important;
}

.brand .slider .bx-wrapper .bx-pager {
  bottom: 2px;
  height: 3px;
  background-color:  rgba(130.0, 130.0, 130.0, 0.15);
  width: 346px;
  padding-top: 0;
  margin-left: 15px;
}

.brand .slider .bx-wrapper .bx-pager {
  display: none;
}

.brand .slider__item {
  width: 100px !important;
}

.brand__block {
  margin-bottom: 250px;
}

.brand__item--fluke {
  margin-left: -40px;
  margin-top: 5px;
}

.brand__item--guide {
  margin-left: -37px;
  margin-top: 2px;
}

.brand__item--pulsar {
  margin-left: -18px;
}

.brand__image--iray {
  width: 54px;
  margin-left: 30px;
}

.brand__image--dedal {
  width: 40px;
  margin-top: -5px;
  margin-left: 5px;
}

.brand__image--seek {
  width: 53px;
  margin-top: 2px;
  margin-left: -25px;
}

.brand__image--fortuna {
  width: 49px;
  margin-left: -47px;
}

.brand__block-wrapper {
  display: none;
}

@media (min-width: $tablet-width) {
  .brand__block {
    margin-bottom: 340px;
  }

  .brand .slider__item--pulsar {
    width: 224px !important;
  }

  .brand .slider__item--fluke {
    width: 176px !important;
  }

  .brand .slider .bx-wrapper .bx-viewport {
    margin-left: 39px;
  }

  .brand__image {


    &--flir {
      width: 115px;
      height: 40px;
    }

    &--atn {
      width: 57px;
      height: 44px;
      margin-left: 30px;
    }

    &--fluke {
      width: 176px;
      height: 35px;
    }

    &--testo {
      width: 44px;
      height: 44px;
      margin-left: 30px;
    }

    &--guide {
      width: 105px;
      height: 30px;
    }

    &--pulsar {
      width: 224px;
      height: 45px;
    }

    &--iray {
      width: 224px;
      height: 45px;
    }

    &--dedal {
      width: 63px;
      height: 54px;
    }

    &--seek {
      width: 98px;
      height: 45px;
    }

    &--fortuna {
      width: 49px;
      height: 44px;
    }
  }

  .brand__item--fluke {
    margin-left: 20px;
    margin-top: 5px;
  }

  .brand__item--guide {
    margin-left: 0;
    margin-top: 5px;
  }

  .brand__item--pulsar {
    margin-left: 30px;
    margin-top: 1px;
  }

  .brand__image--dedal {
    width: 63px;
    margin-left: 50px;
  }

  .brand__image--seek {
    width: 98px;
    margin-left: 35px;
  }

  .brand__image--fortuna {
    width: 49px;
    margin-left: 50px;
    margin-right: 50px;
  }
}

@media (min-width: $desktop-width) {
  .brand__block-wrapper {
    display: block;
    width: 1110px;
    margin: 0 auto;
    margin-bottom: 340px;
  }

  .brand__block {
    display: none;
  }

  .brand__block__list,
  .brand__block__list-second {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
  }

  .brand__block__list-second {
    width: 780px;
    margin: 0 auto;
    margin-top: 61px;
  }

  .brand__image--fortuna {
    width: 211px;
    margin-left: 20px;
  }

  .brand__image--iray {
    margin-left: 0;
  }

  .brand__image--dedal {
    margin-left: 0;
  }
}
